import Header from '../components/layout/Header';
import Nav from '../components/layout/Nav';
import Footer from '../components/layout/Footer.js';
import { Card } from "react-bootstrap";
import { Row } from "react-bootstrap";
import { Col } from 'react-bootstrap';

function Miembros() {

  return (
    <>
      <Header />
      <Nav />
      <>
      <div className='container'>
      <Row xs={1} md={2} className="g-4">
  {Array.from({ length: 1 }).map((_, idx) => (
    <Col>
      <Card>
        <Card.Img variant="top" src="img/Ahri.jpg" />
        <Card.Body>
          <Card.Title>Ahri, The leader</Card.Title>
          <Card.Text>
          Lder, fundadora y vocalista principal de K/DA, Ahri deslumbra a los fans con su carisma y confianza.
          Ella es la pieza central del grupo. De carcter tranquilo, defiende la libertad de expresin y la
          naturalidad, tanto la suya como la de sus compaeras. Ahri se enorgullece de descubrir talentos nuevos
          y de ayudarles a alcanzar su mximo potencial.
          </Card.Text>
        </Card.Body>
      </Card>
    </Col>
  ))}
  {Array.from({ length: 1 }).map((_, idx) => (
    <Col>
      <Card>
        <Card.Img variant="top" src="img/Akali.jpg" />
        <Card.Body>
          <Card.Title>Akali, The rapper</Card.Title>
          <Card.Text>
          Akali es la rapera principal del grupo, con su estilo punk grafitero y su toque audaz y peligroso.
          Ella mantiene al equipo constantemente enfocado y valora su independencia creativa, ante todo.
          Akali siempre est trabajando en proyectos nuevos y se niega a comprometer su arte. Ella tambin
          es rapera en otro grupo llamado True Damage actualmente en hiatus.
          </Card.Text>
        </Card.Body>
      </Card>
    </Col>
  ))}
  {Array.from({ length: 1 }).map((_, idx) => (
    <Col>
      <Card>
        <Card.Img variant="top" src="img/Evelynn.jpg" />
        <Card.Body>
          <Card.Title>Evelynn, The diva</Card.Title>
          <Card.Text>
          Evelynn es la vocalista lder de K/DA y su presencia sobre el escenario es sencillamente hipntica.
          No le gusta estar a la sombra de nadie y se deleita con la reaccin del pblico ante su apariencia
          estilizada. Como cofundadora de K/DA junto con Ahri, Evelynn empuja al grupo hacia sus proyectos
          creativos ms arriesgados y emocionantes.
          </Card.Text>
        </Card.Body>
      </Card>
    </Col>
  ))}
  {Array.from({ length: 1 }).map((_, idx) => (
    <Col>
      <Card>
        <Card.Img variant="top" src="img/Kaisa.jpg" />
        <Card.Body>
          <Card.Title>Kai'sa, The main dancer</Card.Title>
          <Card.Text>
          Como bailarina principal y coregrafa del equipo, Kai'Sa solo se conforma con la perfeccin.
          Su enfoque silencioso pero intenso, su misticismo y su impecable atencin al detalle impulsan a su
          equipo. Kai'Sa tiene una visin cosmopolita de la vida, por lo que cada rincn del mundo la inspira
          en una forma particular.
          </Card.Text>
        </Card.Body>
      </Card>
    </Col>
  ))}
</Row>
</div>


</>


      <Footer />
    </>

  );
}

export default Miembros;